<template lang="html">
    <div class="nav" :class="{ 'is-hidden': !showHeader }">
        <p>
            <a href="#" :class="[(this.$route.fullPath == '/') ? `current` : '']">Home</a>
            <a href="#projects" :class="[(this.$route.fullPath == '/#projects') ? `current` : '']">Projects</a>
            <a href="#contact" :class="[(this.$route.fullPath == '/#contact') ? `current` : '']">Contact</a>
        </p>
    </div>
</template>

<script>
export default {
    data: () => ({
        showHeader: true,
        scrollOffset: 250,
    }),
    async created() {
        window.addEventListener('scroll', this.onScroll)
    },
    beforeUnmount() {
        window.removeEventListener('scroll', this.onScroll)
    },
    methods: {
        // Toggle if navigation is shown or hidden
        onScroll() {
            let projects = document.getElementById('projects').getBoundingClientRect();
            let contact = document.getElementById('contact').getBoundingClientRect();

            // console.log(`${projects.top}: ${contact.top}`);
            if (0 > projects.top-300 && contact.top > 800) {
                this.$router.push("#projects");
            } else if (contact.top < 800) {
                this.$router.push("#contact");
            } else {
                this.$router.push("");
            }
            this.showHeader = window.pageYOffset < this.scrollOffset
        },
    }
}
</script>

<style lang="css" scoped src="@/assets/style/nav.css">
</style>
