<template lang="html">
    <div id="projects" class="projects">
        <template v-for="panel in panelsArray" :key="panel.id">
            <template v-if="panel.id >= pageStart && panel.id < pageStart+itemsShow">
                <div class="panel" :id="`${panel.id}`">
                    <div class="title">
                        <img :src="require(`@/assets/icons/${panel.icon}`)">
                        <h1>{{ panel.name }}</h1>
                        <div class="links">
                            <template v-for="(link, name) in panel.links" :key="link">
                                <a :href="link" target="_blank">
                                    <font-awesome-icon v-if="name==`github`" icon="fa-brands fa-github" title="Github" size="2x" />
                                    <font-awesome-icon v-else-if="name==`reddit`" icon="fa-brands fa-reddit" title="Reddit" size="2x" />
                                    <font-awesome-icon v-else-if="name==`discord`" icon="fa-brands fa-discord" title="Discord" size="2x" />
                                    <font-awesome-icon v-else-if="name==`twitter`" icon="fa-brands fa-twitter" title="Twitter" size="2x" />
                                    <font-awesome-icon v-else-if="name==`facebook`" icon="fa-brands fa-facebook" title="Facebook" size="2x" />
                                    <font-awesome-icon v-else-if="name==`www`" icon="fa-solid fa-link" title="Website" size="2x" />
                                </a>
                            </template>
                        </div>
                    </div>
                    <hr>
                    <div class="content-left">
                        <p v-html="panel.desc" class="desc"></p>
                        <!-- <p class="visit">Visit this project -> <a :href="panel.link">{{ panel.link }}</a></p> -->
                        <div class="user-container">
                            <hr>
                            <p class="users">
                                <img v-for="owner in panel.owners" :key="owner.id" :src="avatar(owner.dcId)" :alt="avatar(owner.dcId)" :title="owner.title">
                            </p>
                        </div>
                    </div>
                    <div class="content-right">
                        <ul>
                            <li>Languages Used:</li>
                            <li v-for="lang in panel.languages" :key="lang.id">
                                <font-awesome-icon v-if="lang.name==`Python`" style="width: 16px;" icon="fa-brands fa-python" size="1x" />
                                <font-awesome-icon v-else-if="lang.name==`Java`" style="width: 16px;" icon="fa-brands fa-java" size="1x" />
                                <font-awesome-icon v-else-if="lang.name==`JavaScript`" style="width: 16px; height: 16px;" icon="fa-brands fa-js" size="1x" />
                                <font-awesome-icon v-else-if="lang.name==`JSON`" style="width: 16px; height: 16px;" icon="fa-brands fa-js" size="1x" />
                                <font-awesome-icon v-else-if="lang.name==`Rust`" icon="fa-brands fa-rust" size="1x" />
