            },
            pageStart: 1,
            itemsShow: 4,
            pages: 0,
            page: 1,
        }
    },
    methods: {
        avatar(id) {
            return this.icons[id];
        },
        nextProject() {
            if (this.pageStart <= panels.length-this.itemsShow) {
                this.pageStart += this.itemsShow;
                this.page += 1;
            } else {
                this.pageStart = 1;
                this.page = 1;
            }
        },
        previousProject() {
            if (this.pageStart > 1) {
                this.pageStart -= this.itemsShow;
                this.page -= 1;
            } else {
                this.page = this.pages;
                this.pageStart = this.page*this.itemsShow-this.itemsShow+1;
            }
        },
        openPage(page) {
            // Go to the first item in range
            this.pageStart = page*this.itemsShow-this.itemsShow+1;
            this.page = page;
        }
    },
    async created () {
        this.pages = Math.ceil(panels.length/this.itemsShow);

