import { createApp } from 'vue'
import router from './router'
import App from './App.vue'
import VueWriter from "vue-writer";


/* import the fontawesome core */
import { library } from '@fortawesome/fontawesome-svg-core'

/* import font awesome icon component */
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

/* import specific icons */
import { faGithub, faReddit, faDiscord, faTwitter, faFacebook, faPython, faJava, faJs, faRust, faPhp, faHtml5, faCss3, faNode, faVuejs, faReact } from '@fortawesome/free-brands-svg-icons'
import { faLink, faEnvelope } from '@fortawesome/free-solid-svg-icons'

/* add icons to the library */
library.add(faGithub, faReddit, faDiscord, faTwitter, faFacebook, faPython, faJava, faJs, faRust, faPhp, faHtml5, faCss3, faNode, faVuejs, faReact, faLink, faEnvelope)

createApp(App)
    .use(router)
    .use(VueWriter)
    .component('font-awesome-icon', FontAwesomeIcon)
    .mount('#app')
